import React from 'react';
import PropTypes from 'prop-types';
import './css/UltimasCarreras.css';

const UltimasCarreras = ({ carreras }) => {
  if (!carreras || carreras.length === 0) return null;

  const ultimas = carreras.slice(-3).reverse(); // Mostrar las últimas 3, la más reciente arriba

  return (
    <article className="ultimas-carreras">
      <h4>Last Races</h4>
      <div className="laminillas-container">
        {ultimas.map((carrera, idx) => (
          <div key={idx} className="laminilla" style={{ '--i': idx }}>
            <div className="contenido">
              <span className="carrera-num">#{carrera.race}</span>
              <span className="lugar">{carrera.Lugar ?? carrera.lugar ?? 'N/A'}</span>
            </div>
          </div>
        ))}
      </div>
    </article>
  );
};

UltimasCarreras.propTypes = {
  carreras: PropTypes.arrayOf(PropTypes.object),
};

export default UltimasCarreras;

