import React from 'react';
import PropTypes from 'prop-types';
import './css/AccionesBotones.css';

import logos from '../../../../../../../../../../assets/logoblockhorseracing/coinBHRT2.png';
import logosBHR from '../../../../../../../../../../assets/logoblockhorseracing/logoBlock.png';
import logoHive from '../../../../../../../../../../assets/logoblockhorseracing/logoHive.png';

const getLogoBySymbol = (symbol) => {
  switch (symbol?.toUpperCase()) {
    case 'BHRT':
      return logos;
    case 'BHR':
      return logosBHR;
    case 'HIVE':
      return logoHive;
    default:
      return null; // Puedes colocar logo por defecto si quieres
  }
};

const AccionesBotones = ({ firmando, onConfirmar, onVolver, raceData }) => {
  const logoSrc = getLogoBySymbol(raceData?.symbol_1);

  return (
    <div className="acciones-auspicio">
      {raceData !== undefined && (
        <div className="fee-text-auspicio">
          Fee: <strong>{raceData.costo_registro} {raceData.symbol_1}</strong>
          {logoSrc && (
            <img
              src={logoSrc}
              alt={raceData.symbol_1}
              title={raceData.symbol_1}
              className="fee-symbol-logo"
            />
          )}
        </div>
      )}

      <button
        onClick={onConfirmar}
        disabled={firmando}
        className="confirmar-auspicio"
        aria-busy={firmando}
      >
        {firmando ? 'Signing...' : 'Sponsor'}
      </button>

      <button
        className="back-button"
        onClick={onVolver}
        disabled={firmando}
      >
        Back
      </button>
    </div>
  );
};

AccionesBotones.propTypes = {
  firmando: PropTypes.bool.isRequired,
  onConfirmar: PropTypes.func.isRequired,
  onVolver: PropTypes.func.isRequired,
  raceData: PropTypes.object,
};

AccionesBotones.defaultProps = {
  raceData: null,
};

export default AccionesBotones;

