import { useState, useEffect, useRef } from 'react';
import { linkApp } from './../../components/generals/configuracion/variablesPublicas.jsx';

export function useUltimasParticipaciones(usuario) {
  const [participaciones, setParticipaciones] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const isMounted = useRef(true);

  useEffect(() => {
    isMounted.current = true;

    if (!usuario) {
      if (isMounted.current) setParticipaciones([]);
      return;
    }

    const fetchUltimasParticipaciones = async () => {
      if (!isMounted.current) return;

      setLoading(true);
      setError(null);

      try {
        const url = `${linkApp}/participantes/ultimas-participaciones?usuario=${encodeURIComponent(usuario)}`;
        console.log('Fetch URL en useUltimasParticipaciones:', url);

        const response = await fetch(url);
        const text = await response.text();


console.log('Respuesta cruda:', text);

        console.log('Respuesta cruda en useUltimasParticipaciones:', text);

        let data;
        try {
          data = JSON.parse(text);
        } catch (parseError) {
          throw new Error('La respuesta no es JSON válido');
        }

        if (data.error) {
          throw new Error(data.error);
        }

        const participacionesNuevas = data.body || [];
        if (!isMounted.current) return;

        // Actualizar estado con las participaciones recibidas (puedes aplicar lógica para evitar setState innecesarios)
        setParticipaciones((prev) => {
          try {
            const iguales = JSON.stringify(prev) === JSON.stringify(participacionesNuevas);
            return iguales ? prev : participacionesNuevas;
          } catch {
            return participacionesNuevas;
          }
        });

      } catch (err) {
        if (!isMounted.current) return;
        setError(err.message || 'Error al cargar participaciones');
      } finally {
        if (isMounted.current) setLoading(false);
      }
    };

    fetchUltimasParticipaciones();

    return () => {
      isMounted.current = false;
    };
  }, [usuario]);

  return { participaciones, loading, error };
}
