import React from 'react';
import { useTranslation } from 'react-i18next';
import './css/CardResumen.css'
import logos from '../../../../../../../assets/logoblockhorseracing/logoBlock.png';


export default function CardResumen({ usuario, onOpenModal }) {
  const { t } = useTranslation();

  return (
    <div className="history-card-container">
      {/* El logo ahora es un elemento separado, para que podamos centrarlo */}
      <img src={logos} alt="BHR" className="logo-historial" />
      <button className="view-horses-button" onClick={onOpenModal}>
        {t('viewRegisteredHorsesHistory')}
      </button>
    </div>
  );
}