import { useState, useEffect } from 'react';
import { linkApp } from './../../components/generals/configuracion/variablesPublicas.jsx';

async function fetchCaballosPorRareza(rareza) {
  if (!rareza) return [];

  try {
    const response = await fetch(`${linkApp}/equino/filtrados_auspicio?rareza=${encodeURIComponent(rareza)}`);
    if (!response.ok) {
      throw new Error(`Error en la petición: ${response.statusText}`);
    }
    const data = await response.json();

    if (data.error && data.error !== "") {
      throw new Error(data.error);
    }

    // Retorna solo el arreglo 'body' con los caballos
    return data.body || [];
  } catch (error) {
    throw error;
  }
}

export function useCaballosPorRareza(rareza) {
  const [caballos, setCaballos] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  useEffect(() => {
    if (!rareza) {
      setCaballos([]);
      return;
    }

    setLoading(true);
    setError(null);

    fetchCaballosPorRareza(rareza)
      .then((data) => {
        setCaballos(data);
        setLoading(false);
      })
      .catch((err) => {
        setError(err);
        setLoading(false);
      });
  }, [rareza]);

  return { caballos, loading, error };
}
