import React, { useState, useEffect } from 'react';

import Icon from './../../generals/svgiconos/svgiconosenreact.jsx';
import Loader from './../../generals/loders/LoaderRunHorse.jsx';
import ModalPatrocinarBallo from './modals/ModalPatrocinarBallo.jsx';
import HorseCard from './cards/HCardTest.jsx';
import SponsorHorseCard from './cards/SponsorHorseCard.jsx';

import useTorneoData from './../../../hooks/torneo/useTorneoData';

const SponsorList = ({ horses, horsesHabilitados, openModal, loadingHorse, loadingHorseB }) => {
  const [selectedHorse, setSelectedHorse] = useState([]);
  const [isVisibleMail, setIsVisibleMail] = useState(false);
  const [user, setUser] = useState(localStorage.getItem("user"));

  const { rarezaMax } = useTorneoData(true);

  useEffect(() => {
    if (process.env.NODE_ENV === 'development') {
      console.log("rarezaMax:", rarezaMax);
    }
  }, [rarezaMax]);

  const nivelesRareza = ['common', 'uncommon', 'rare', 'epic', 'legendary'];

  const caballosPermitidos = Array.isArray(horses) && rarezaMax
    ? horses.filter(horse => {
        const horseRareza = horse?.tipo?.toLowerCase?.();
        const rarezaMaxLower = rarezaMax?.toLowerCase?.();
        const indexMax = nivelesRareza.indexOf(rarezaMaxLower);
        const indexHorse = nivelesRareza.indexOf(horseRareza);
        return indexHorse !== -1 && indexHorse <= indexMax;
      })
    : [];

  const horsesHabilitadosExternos = horsesHabilitados.filter(
    (horse) => horse.account !== user
  );

  const openModalSponsorBallo = (horse) => {
    setSelectedHorse(horse);
    if (horse) setIsVisibleMail(true);
  };

  const toggleOpenMail = () => {
    setIsVisibleMail(!isVisibleMail);
  };

  const closeModalSponsorBallo = () => {
    setIsVisibleMail(false);
    setSelectedHorse(null);
  };

  return (
    <div className="TicketModule">
      {horsesHabilitadosExternos.length >= 8 ? (
        <div className="text-yellow-600 font-semibold mb-4">
          You have already enabled 8 horses. You can't sponsor more at this time.
        </div>
      ) : (
        <>
          <h2>Horses available for sponsorship</h2>
          <div className="SponsorModule">
            {loadingHorse ? (
              <Loader />
            ) : (
              <ul>
                {caballosPermitidos.length > 0 ? (
                  caballosPermitidos.map(horse => (
                    <SponsorHorseCard
                      key={horse.equineId}
                      horse={horse}
                      openModalSponsorBallo={openModalSponsorBallo}
                    />
                  ))
                ) : (
                  <p>There are no horses available for sponsorship</p>
                )}
              </ul>
            )}
          </div>
        </>
      )}

      <h2>Horses you've enabled for this tournament</h2>
      {loadingHorseB ? (
        <Loader />
      ) : (
        <div className="SponsorModule">
          <div className="app">
            {horsesHabilitadosExternos.map((horse) => (
              <HorseCard key={horse.equineId} horse={horse} />
            ))}
          </div>
        </div>
      )}

      {isVisibleMail && (
        <ModalPatrocinarBallo
          closeModal={closeModalSponsorBallo}
          toggleOpenMail={toggleOpenMail}
          horses={selectedHorse}
          isVisibleMail={isVisibleMail}
        />
      )}
    </div>
  );
};

export default SponsorList;




