import React, { useState } from 'react';
import Loader from './../../../../../../generals/loders/Loader.jsx';
import EquinoRow from './equinostabla/EquinoRowNew.jsx';
import AuspiciarButton from './auspiciarHorse/auspiciarButton.jsx';
import AuspiciarModulo from './auspiciarHorse/AuspiciarModulo.jsx';
import './css/equinostable.css';
import './css/equinostable.mobile.css';
import hivelogo from '../../../../../../../assets/logoblockhorseracing/logoHive.png';

const EquinosTable = ({ equinos, fetchApuestas }) => {
  const { equinosT, raceId, raceName, apuestas, raceData } = equinos;
  // console.log("TODO LOS DATOS CREO carrera",raceData)
  const [isLoadingPatrocinio, setIsLoadingPatrocinio] = useState(true);
  const [mostrarAuspicio, setMostrarAuspicio] = useState(false);

  setTimeout(() => setIsLoadingPatrocinio(false), 2);

  const abrirAuspicio = () => setMostrarAuspicio(true);
  const cerrarAuspicio = () => setMostrarAuspicio(false);
 const isMobile = window.innerWidth < 600;
return (
  <div>
    {isLoadingPatrocinio ? (
      <>
        <Loader />
        <p>Loading...</p>
      </>
    ) : (
      <>
        {isMobile ? (// Versión móvil
<div className="equinos-mobile">
  {/* Header info carrera */}
  <div className="equinos-mobile-header">
    <div className="race-id">Race: {raceId}</div>
    <div className="race-name">{raceName}</div>
    <div className="total-pote">
      💰{apuestas.Total_Pote.toFixed(2)}
      <img src={hivelogo} alt="Token" className="coin" />
    </div>
  </div>

  {/* Botón auspiciar */}
  <div className="equinos-mobile-actions">
    <AuspiciarButton
      participantesCount={equinosT.length}
      onClick={abrirAuspicio}
    />
  </div>

  {/* Lista de tarjetas */}
  <div className="equinos-mobile-list">
    {equinosT
      .reduce((uniqueEquinos, eq) => {
        if (!uniqueEquinos.some(uniqueEq => uniqueEq.equineId === eq.equineId)) {
          uniqueEquinos.push(eq);
        }
        return uniqueEquinos;
      }, [])
      .map((eq, index) => (
        <EquinoRow
          key={eq.equineId}
          equino={eq}
          index={index}
          raceId={raceId}
          apuestas={apuestas}
          fetchApuestas={fetchApuestas}
          isMobile={true}
        />
      ))}
  </div>
</div>

        ) : (
          // Versión escritorio original
          <table className="equinos">
            <thead>
              <tr>
                <th>Race: {raceId}</th>
                <th colSpan="3">{raceName}</th>
                <th>
                  <h1>
                    💰{apuestas.Total_Pote.toFixed(2)}{" "}
                    <img src={hivelogo} alt="Token" className="coin" />
                  </h1>
                </th>
              </tr>
            </thead>

            <AuspiciarButton
              participantesCount={equinosT.length}
              onClick={abrirAuspicio}
            />

            <tbody>
              {equinosT
                .reduce((uniqueEquinos, eq) => {
                  if (!uniqueEquinos.some(uniqueEq => uniqueEq.equineId === eq.equineId)) {
                    uniqueEquinos.push(eq);
                  }
                  return uniqueEquinos;
                }, [])
                .map((eq, index) => (
                  <EquinoRow
                    key={eq.equineId}
                    equino={eq}
                    index={index}
                    raceId={raceId}
                    apuestas={apuestas}
                    fetchApuestas={fetchApuestas}
                    isMobile={false}
                  />
                ))}
            </tbody>
          </table>
        )}

        {mostrarAuspicio && (
          <AuspiciarModulo raceData={raceData} onCerrar={cerrarAuspicio} />
        )}
      </>
    )}
  </div>
);

};

export default EquinosTable;
