// useWalletData.js
import { useState, useEffect } from 'react';
import { obtenerDatosWallet } from './../../components/generals/servicios/wallet/getSaldosW.jsx';

const useWalletData = (isVisibleMail, user) => {
  const [saldo, setSaldo] = useState(null);
  const [saldoBhrt, setSaldoBhrt] = useState(null);
  const [saldoBhr, setSaldoBhr] = useState(null);
  const [statusRetiro, setStatusRetiro] = useState(null);
  const [tickets, setTickets] = useState(null);
  const [error, setError] = useState(null);

  useEffect(() => {
    if (isVisibleMail && user) {
      const fetchData = async () => {
        try {
          const resultado = await obtenerDatosWallet(user);
          const { saldo: s, ganancia_a, ganancia_b, ganancia_BHR, status_retiro, tickets } = resultado.data;

          setSaldo(s);
          setSaldoBhrt(ganancia_b);
          setSaldoBhr(ganancia_BHR);
          setStatusRetiro(status_retiro);
          setTickets(tickets);
        } catch (error) {
          setError(error.message);
          console.error(error);
        }
      };

      fetchData();
    }
  }, [isVisibleMail, user]);

  return {
    saldo,
    saldoBhrt,
    saldoBhr,
    statusRetiro,
    tickets,
    error,
  };
};

export default useWalletData;
