import { useState, useEffect } from 'react';
import { linkApp } from './../../components/generals/configuracion/variablesPublicas.jsx';

export function useActuaciones(raceId, shouldFetch) {
  const [actuacionesData, setActuacionesData] = useState([]);
  const [error, setError] = useState(null);
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    // Validación de entrada; si no cumple se limpia estado y sale
    if (!raceId?.raceid || !shouldFetch) {
      setActuacionesData([]);
      setError(null);
      setIsLoading(false);
      return;
    }

    let isMounted = true; // Para evitar setState si desmontado

    const fetchActuaciones = async () => {
      setIsLoading(true);
      setError(null);

      try {
        const response = await fetch(`${linkApp}/actuaciones?race=${raceId.raceid}`);

        if (!response.ok) {
          throw new Error(`HTTP error ${response.status}`);
        }

        const data = await response.json();

        if (!isMounted) return; // Evitar setState si desmontado

        if (data.error) {
          setError(data.error);
          setActuacionesData([]);
        } else {
          setActuacionesData(data.body);
        }
      } catch (e) {
        if (!isMounted) return; // Evitar setState si desmontado
        setError('Error al cargar actuaciones.');
        setActuacionesData([]);
      } finally {
        if (isMounted) setIsLoading(false);
      }
    };

    fetchActuaciones();

    // Cleanup para evitar setState después de desmontar
    return () => {
      isMounted = false;
    };
  }, [raceId?.raceid, shouldFetch]); // Dependencias sólo lo estrictamente necesario

  return { actuacionesData, error, isLoading };
}
