import React from 'react';
import hivelogo from '../../../../../../../../../assets/logoblockhorseracing/logoHive.png';
import logosBHR from '../../../../../../../../../assets/logoblockhorseracing/logoBlock.png';
import logosBHRT from '../../../../../../../../../assets/logoblockhorseracing/coinBHRT2.png';
import  "./PoolsTable.css"

const PoolsTable = ({ swapHive, bhrt, bhr }) => {
  // Función para obtener el logo según el pool
  const getIconBySymbol = (symbol) => {
    switch (symbol) {
      case 'BHRT':
        return logosBHRT;
      case 'SWAP.HIVE':
        return hivelogo;
      case 'BHR':
        return logosBHR;
      default:
        return null;
    }
  };

  // Datos de los pools para iterar
  const pools = [
    { symbol: 'BHRT', amount: bhrt },
    { symbol: 'BHR', amount: bhr },
    { symbol: 'SWAP.HIVE', amount: swapHive },
  ];

  return (
    <div className="pools-table-container">
      <h3>Resumen de Pools</h3>
      <table className="pools-table">
        <thead>
          
             Total Pool 
          
        </thead>
        <tbody>
          {pools.map(({ symbol, amount }) => (
            <tr key={symbol}>
              <td className="token-cell">
                <img
                  src={getIconBySymbol(symbol)}
                  alt={`${symbol} logo`}
                  style={{ width: 24, height: 24, marginRight: 8 }}
                />
                {symbol}
              </td>
              <td>
  {amount !== null && amount !== undefined && !isNaN(amount)
    ? Number(amount).toFixed(3)
    : '-'}
</td>

            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default PoolsTable;
