// src/hooks/useVisualYParticipantesRegistrados.js
import { useEffect, useState, useRef } from 'react';

export const useVisualYParticipantesRegistrados = (raceData, equinosNormalizados, registrados) => {
  const [visual, setVisual] = useState([]);
  const [showParticipantes, setShowParticipantes] = useState(false);
  const [participantesRegistrados, setParticipantesRegistrados] = useState([]);

  // Referencias para valores previos y evitar actualizaciones innecesarias
  const prevShowParticipantes = useRef(null);
  const prevParticipantesRegistrados = useRef(null);
  const prevVisual = useRef(null);

  useEffect(() => {
    const nuevoVisual = raceData?.visual || [];
    // Comparar referencia o contenido para evitar setState redundante
    const mismoVisual = prevVisual.current && JSON.stringify(prevVisual.current) === JSON.stringify(nuevoVisual);
    if (!mismoVisual) {
      setVisual(nuevoVisual);
      prevVisual.current = nuevoVisual;
    }
  }, [raceData]);

  useEffect(() => {
    const nuevoShow = equinosNormalizados.length > 0;
    if (prevShowParticipantes.current !== nuevoShow) {
      setShowParticipantes(nuevoShow);
      prevShowParticipantes.current = nuevoShow;
    }
  }, [equinosNormalizados]);

  useEffect(() => {
    if (equinosNormalizados.length > 0 && registrados.length > 0) {
      const nuevoArray = registrados
        .map((id) => equinosNormalizados.find(e => String(e.equineId) === String(id)))
        .filter(Boolean)
        .map(equino => ({
          name: equino.name || 'Sin nombre',
          points: equino.equineId,
        }));

      const mismoArray = prevParticipantesRegistrados.current && 
        JSON.stringify(prevParticipantesRegistrados.current) === JSON.stringify(nuevoArray);

      if (!mismoArray) {
        setParticipantesRegistrados(nuevoArray);
        prevParticipantesRegistrados.current = nuevoArray;
      }
    } else {
      if (prevParticipantesRegistrados.current !== null && prevParticipantesRegistrados.current.length !== 0) {
        setParticipantesRegistrados([]);
        prevParticipantesRegistrados.current = [];
      }
    }
  }, [equinosNormalizados, registrados]);

  return { visual, showParticipantes, participantesRegistrados };
};
