import PropTypes from 'prop-types';
import Icon from './../../../../../../../../../generals/svgiconos/svgiconosenreact.jsx';
import './css/DatosCaballo.css';

const DatosCaballo = ({ equino }) => {
  return (
    <article className="horse-details__auspiciar">
      <h3 className="horse-details__title__auspiciar">Horse Info</h3>

      <div className="horse-details__info__auspiciar">
        {/* Columna izquierda: Nombre y Rareza */}
        <div className="horse-details__col__auspiciar">
          <p className="horse-details__nombre__auspiciar">
            {equino.nameBhr || equino.name || 'No name'}
          </p>
          <p className="horse-details__rareza__auspiciar">
            {equino.tipo || 'Unknown'}
          </p>
        </div>

        {/* Columna derecha: SVG e ID */}
        <div className="horse-details__col__auspiciar horse-details__col--right__auspiciar">
          <div className="horse-details__image__auspiciar">
            <Icon name="horsehead" className="horse-details__svg__auspiciar" alt="Speed" />
          </div>
          <p className="horse-details__id__auspiciar">{equino.equineId}</p>
        </div>
      </div>
    </article>
  );
};

DatosCaballo.propTypes = {
  equino: PropTypes.shape({
    nameBhr: PropTypes.string,
    name: PropTypes.string,
    equineId: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
    tipo: PropTypes.string,
  }).isRequired,
};

export default DatosCaballo;

