export function esPremioValido(valor) {
  if (valor === null || valor === undefined) return false;
  if (typeof valor === 'string') {
    const valTrim = valor.trim().toUpperCase();
    if (valTrim === 'N/A' || valTrim === '' || valTrim === '0') return false;
    const num = Number(valor);
    if (!isNaN(num) && num === 0) return false;
    return true;
  }
  if (typeof valor === 'number') {
    return valor !== 0;
  }
  return Boolean(valor);
}

export function filtrarClavesValidas(participaciones) {
  return participaciones
    .map(p => ({ race: Number(p.race), equine: p.equineId }))
    .filter(({ race, equine }) => race && equine !== null && equine !== undefined);
}

export function combinarDatos(participaciones, rewards) {
  return participaciones.map(p => {
    const extra = rewards.find(r => r.race === Number(p.race) && r.equine === p.equineId) || {};
    return {
      trx_Registro: p.trx_Registro,
      usuario: p.usuario,
      race: p.race,
      equineId: p.equineId,
      equinoName: p.equinoName,
      puesto_Llegada: extra.llegada || p.puesto_Llegada || 'N/D',
      r_quantity: p.r_quantity || 'N/A',
      r_symbol: p.r_symbol || 'N/A',
      r_recibe: p.r_recibe || 'N/A',
      premios: {
        oficial: {
          premio_1: extra.premio_1,
          premio_2: extra.premio_2,
          premio_3: extra.premio_3,
          premio_4: extra.premio_4,
          premio_e: extra.premio_e,
          trx_1: extra.trx_1,
          trx_2: extra.trx_2,
          trx_3: extra.trx_3,
          trx_4: extra.trx_4,
          trx_e: extra.trx_e,
        },
        estados: {
          status_1: extra.status_1,
          status_2: extra.status_2,
          status_3: extra.status_3,
          status_4: extra.status_4,
          status_e: extra.status_e,
          status_pago: extra.status_pago,
        },
      },
    };
  });
}
