import { useState, useEffect, useRef } from 'react';
import { linkApp } from './../../components/generals/configuracion/variablesPublicas.jsx';

/**
 * Hook para obtener datos de rewardsRace dado un array de claves {race, equine}
 * @param {Array<{race: number | string, equine: number}>} claves - Array con las claves de consulta
 * @returns {Object} { info, loading, error }
 */
export function useRewardsRace(claves = []) {
  const [info, setInfo] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const isMounted = useRef(true);

  useEffect(() => {
    isMounted.current = true;

    // Filtrar claves inválidas para evitar llamadas innecesarias
    const clavesValidas = Array.isArray(claves)
      ? claves.filter(({ race, equine }) => {
          const raceOk = race !== null && race !== undefined && race !== '';
          const equineOk = equine !== null && equine !== undefined;
          return raceOk && equineOk;
        })
      : [];

    if (clavesValidas.length === 0) {
      if (isMounted.current) setInfo([]);
      return;
    }

    const fetchRewards = async () => {
      if (!isMounted.current) return;
      setLoading(true);
      setError(null);

      try {
        const url = `${linkApp}/rewardrace`; // URL correcta según backend
        console.log('Fetch URL en useRewardsRace:', url);

        const response = await fetch(url, {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({ claves: clavesValidas }), // enviar las claves filtradas
        });

        const text = await response.text();
        console.log('Respuesta cruda en useRewardsRace:', text);

        let data;
        try {
          data = JSON.parse(text);
        } catch (parseError) {
          throw new Error('La respuesta no es JSON válido');
        }

        if (data.error) {
          throw new Error(data.error);
        }

        const rewardsNuevos = data.body || [];
        if (!isMounted.current) return;

        setInfo((prev) => {
          try {
            const iguales = JSON.stringify(prev) === JSON.stringify(rewardsNuevos);
            return iguales ? prev : rewardsNuevos;
          } catch {
            return rewardsNuevos;
          }
        });
      } catch (err) {
        if (!isMounted.current) return;
        setError(err.message || 'Error al cargar datos de rewards');
      } finally {
        if (isMounted.current) setLoading(false);
      }
    };

    fetchRewards();

    return () => {
      isMounted.current = false;
    };
  }, [claves]);

  return { info, loading, error };
}
