const normalizarEquino = (eq) => {
  const resultado = {
    equineId: eq.equineId ?? 0,
    account: eq.account ?? 'desconocido',
    agilidad: String(eq.agilidad ?? '0'),
    alimentos_ptos: eq.alimentos_ptos ?? 0,
    animo: String(eq.animo ?? '0'),
    carreras: eq.carreras ?? [],
    implementos_ptos: eq.implementos_ptos ?? 0,
    name: eq.name || eq.nameBhr || 'Sin nombre',
    nameBhr: eq.nameBhr || '',
    resistencia: String(eq.resistencia ?? '0'),
    salud: String(eq.salud ?? '0'),
    sexo: eq.sexo ?? 'N/A',
    tipo: eq.tipo ?? 'Desconocido',
    velocidad: String(eq.velocidad ?? '0'),
    resistencia_add: String(eq.resistencia_add ?? '0'),
    habilitado: eq.habilitado ?? 'no',
    habilitador: eq.habilitador ?? '',
  };

  console.log('Normalizando equino:', eq, '=>', resultado);

  return resultado;
};

const normalizarParticipante = (p) => {
  const resultado = {
    equineId: p.equineId ?? 0,
    account: p.account ?? 'desconocido',
    agilidad: String(p.agilidad ?? '0'),
    animo: String(p.animo ?? '0'),
    carreras: p.carreras ?? [],
    name: p.nameBhr || p.equinoName || 'Sin nombre',
    nameBhr: p.equinoName || '',
    resistencia: String(p.resistencia ?? '0'),
    tipo: p.tipo || 'Desconocido',
    velocidad: String(p.velocidad ?? '0'),
    salud: '100',          // no viene en participante, pon valor por defecto
    alimentos_ptos: 0,  // idem
    implementos_ptos:String(p.implementos_ptos ?? '0'), // idem
    habilitado: p.sponsor || 'no',    // idem
    habilitador: p.usuario || p.account,     // idem
  };

  console.log('Normalizando participante:', p, '=>', resultado);

  return resultado;
};


// Función para validar si un equino normalizado tiene datos válidos
const esEquinoValido = (eq) => {
  if (!eq) return false;
  if (!eq.equineId) return false;
  if (!eq.name || eq.name === 'Sin nombre') return false;
  if (!eq.tipo || eq.tipo === 'Desconocido') return false;

  if (isNaN(Number(eq.agilidad)) || Number(eq.agilidad) <= 0) return false;
  if (isNaN(Number(eq.velocidad)) || Number(eq.velocidad) <= 0) return false;
  if (isNaN(Number(eq.animo)) || Number(eq.animo) < 0) return false;
  if (isNaN(Number(eq.resistencia)) || Number(eq.resistencia) <= 0) return false;
  return true;
};





module.exports = { normalizarEquino, normalizarParticipante, esEquinoValido };
