import { useEffect, useState, useRef, useCallback } from 'react';
import { linkApp } from './../../components/generals/configuracion/variablesPublicas.jsx';

export const useApuestas = (raceId) => {
  const [apuestas, setApuestas] = useState([]);
  const [errorApuestas, setErrorApuestas] = useState(null);
  const [isLoading, setIsLoading] = useState(false);

  const isMounted = useRef(true);

  // Función fetch memoizada para evitar recreaciones constantes
  const fetchApuestas = useCallback(async () => {
    if (!raceId) return;
    setIsLoading(true);

    try {
      const response = await fetch(`${linkApp}/bets?race=${raceId}`);

      if (!response.ok) {
        throw new Error(`HTTP error ${response.status}`);
      }

      const data = await response.json();

      if (!isMounted.current) return;

      if (data.error) {
        setErrorApuestas(data.error);
      } else {
        // Actualiza solo si cambian los datos para evitar re-render innecesario
        setApuestas(prev => {
          const nuevo = data.body[0] || [];
          try {
            const iguales = JSON.stringify(prev) === JSON.stringify(nuevo);
            return iguales ? prev : nuevo;
          } catch {
            return nuevo;
          }
        });
        setErrorApuestas(null);
      }
    } catch (e) {
      if (!isMounted.current) return;
      setErrorApuestas('Error al cargar las Apuestas.');
    } finally {
      if (isMounted.current) setIsLoading(false);
    }
  }, [raceId]);

  useEffect(() => {
    isMounted.current = true;
    fetchApuestas();
    const interval = setInterval(() => fetchApuestas(), 40000);

    return () => {
      isMounted.current = false;
      clearInterval(interval);
    };
  }, [fetchApuestas]);

  return { apuestas, errorApuestas, isLoading, fetchApuestas };
};
