// auspiciarHorse/AuspiciarButton.jsx
import React from 'react';
import { useTranslation } from 'react-i18next';

const AuspiciarButton = ({ participantesCount, onClick }) => {
   const { t } = useTranslation();
  
  if (participantesCount >= 8) {
    return null; // No mostrar el botón si hay 8 o más participantes
  }

return (
<tr>
  <td colSpan="5" style={{ textAlign: 'center', border : 'none'  }}>
    <button onClick={onClick} className="auspiciar-button-pro">
     {t('auspiciar.AuspiciarBallo')}
    </button>
  </td>
</tr>


);
};

export default AuspiciarButton;
