import React from "react";

import './cssModularizado/index.css';
import ReactDOM from "react-dom/client";
import {
  createBrowserRouter,
  RouterProvider,

} from "react-router-dom";
import { consol } from './components/generals/configuracion/variablesPublicas.jsx';



import App from './App';
import MainPre from "./components/main/Main_pre";
import MainDash from "./components/main/Main_Dash.jsx";
import MainAuction from "./components/main/Main_auc.jsx";
import MainMarket  from "./components/main/Market.jsx";
// import MainStatistic  from "./components/main/Main_Statistic.jsx";
import Main_race from"./components/main/Main_race.jsx";


import ErrorPage from "./components/error/errorPage.jsx";
import BurnToVote from "./components/pages/dashboard/burntovote/burnToVote.jsx"; // importa tu nuevo componente





if (consol==false) {
  console.log = () => {};
  console.error = () => {};
  console.warn = () => {};
}
// console.log("Environment:", process.env.NODE_ENV);




const router = createBrowserRouter([
  {
    path: "/",
    element: <App/>,
    errorElement: <ErrorPage />,
    children: [
      {
        path: "/dashboard",
        element: <MainDash/>,
      },
      {
        path: "/",
        element: <MainPre/>,
      },
      {
        path: "/market",
        element: <MainMarket/>,
      },
      {
        path: "/races",
        element: <Main_race/>,
      },
      {
        path: "/burntovote",
        element: <BurnToVote/>,   // <--- aquí agregas la nueva ruta
      },
    ],
  },
]);

ReactDOM.createRoot(document.getElementById("root")).render(
  <React.StrictMode>
    <RouterProvider router={router} />
  </React.StrictMode>
);
