import { useState, useEffect } from 'react';
import { linkApp } from './../../components/generals/configuracion/variablesPublicas.jsx';

export function useEquino(equineId) {
  const [equino, setEquino] = useState(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  useEffect(() => {
    if (!equineId) return;

    setLoading(true);
    setError(null);

    const url = `${linkApp}/equino?equineId=${equineId}`;

    fetch(url)
      .then((res) => {
        if (!res.ok) throw new Error(`Error ${res.status}`);
        return res.json();
      })
      .then((data) => {
        if (data.error) {
          throw new Error(data.error);
        }
        if (data.body && Array.isArray(data.body) && data.body.length > 0) {
          setEquino(data.body[0]);  // Asignamos el primer objeto del array body
        } else {
          setEquino(null);  // No hay datos
        }
        setLoading(false);
      })
      .catch((err) => {
        setError(err);
        setLoading(false);
      });
  }, [equineId]);

  return { equino, loading, error };
}
