import React, { useMemo } from 'react';
import { useUltimasParticipaciones } from './../../../../../../../hooks/history/useLastAuspicios.jsx';
import { useRewardsRace } from './../../../../../../../hooks/history/useRewardsRaces.jsx';
import ParticipacionItem from './components/ParticipacionItem.jsx';
import { filtrarClavesValidas, combinarDatos } from './components/utils.jsx';
import './css/ModalAuspicio.css';
import { useTranslation } from 'react-i18next';

export default function ModalAuspicio({ usuario, onClose, isOpen }) {
  const { t } = useTranslation();

  const { participaciones, loading: loadingP, error: errorP } = useUltimasParticipaciones(usuario);
  const clavesValidas = useMemo(() => filtrarClavesValidas(participaciones), [participaciones]);
  const { info: rewards, loading: loadingI, error: errorI } = useRewardsRace(clavesValidas);

  const datosCombinados = useMemo(() => combinarDatos(participaciones, rewards), [participaciones, rewards]);

  const datosAMostrar = datosCombinados.filter(
    d =>
      d.race !== null && d.race !== undefined &&
      d.equineId !== null && d.equineId !== undefined
  );

  if (!isOpen) return null;
  if (loadingP || loadingI) return <p className="history-sponsor-loading">{t('loadingData')}</p>;
  if (errorP || errorI) return <p className="history-sponsor-error">{t('error')}: {errorP || errorI}</p>;
  if (datosAMostrar.length === 0) return <p className="history-sponsor-no-data">{t('noValidData')}</p>;

  return (
    <div className={`history-sponsor-modal-backdrop${isOpen ? ' is-open' : ''}`} onClick={onClose}>
      <div className="history-sponsor-modal-container" onClick={e => e.stopPropagation()}>
        <button onClick={onClose} className="history-sponsor-close-button">{t('close')}</button>
        <h2 className="history-sponsor-title">{t('detailedParticipations', { usuario })}</h2>
        <ul className="history-sponsor-list">
          {datosAMostrar.map(d => (
            <ParticipacionItem key={d.trx_Registro} d={d} />
          ))}
        </ul>
      </div>
    </div>
  );
}
