import React from 'react';
import { esPremioValido } from './utils.jsx';
import { useTranslation } from 'react-i18next';

// Función para formatear número según primeros 4 decimales
function formatearNumeroPremio(valor) {
  const num = Number(valor);
  if (isNaN(num)) return valor; // Si no es número, devolver tal cual
  const decimales = num.toString().split('.')[1] || '';
  const primeros4 = decimales.slice(0, 4);
  if (primeros4 === '' || /^0{1,4}$/.test(primeros4)) {
    return num.toFixed(0); // Mostrar entero
  }
  return num.toFixed(4).replace(/\.?0+$/, ''); // Hasta 4 decimales sin ceros finales
}

export default function PremiosList({ premios }) {
  const { t } = useTranslation();

  const premiosData = [
    { label: t('prize1'), value: premios.premio_1, trx: premios.trx_1 },
    { label: t('prize2'), value: premios.premio_2, trx: premios.trx_2 },
    { label: t('prize3'), value: premios.premio_3, trx: premios.trx_3 },
    { label: t('prize4'), value: premios.premio_4, trx: premios.trx_4 },
    { label: t('specialPrize'), value: premios.premio_e } // Sin trx
  ];

  const formatHash = (hash) => {
    if (!hash || hash === 'N/A') return 'N/A';
    return hash.slice(-5);
  };

  return (
    <ul className="history-sponsor-sub-list">
      {premiosData.map(({ label, value, trx }, i) => {
        if (!esPremioValido(value)) return null;
        return (
          <li key={i}>
            {label}: {formatearNumeroPremio(value)}{' '}
            {label === t('specialPrize') ? (
              <em>({t('specialChest')})</em>
            ) : (
              trx && trx !== 'N/A' && (
                <>
                  ({t('trx')}:&nbsp;
                  <a
                    href={`https://he.dtools.dev/tx/${trx}`}
                    target="_blank"
                    rel="noopener noreferrer"
                    style={{ color: '#94a3b8', textDecoration: 'underline' }}
                    title={trx}
                  >
                    {formatHash(trx)}
                  </a>
                  )
                </>
              )
            )}
          </li>
        );
      })}
    </ul>
  );
}
