import React from 'react';
import PropTypes from 'prop-types';
import './css/EstadisticaCaballo.css';
import Icon from './../../../../../../../../../generals/svgiconos/svgiconosenreact.jsx';
import { Stars } from './Components/Stars.jsx';

const EstadisticasCaballo = ({ velocidad, resistencia, agilidad }) => (
  <article className="estadisticas neon-stars-wrapper">
    <div className="stat">
      <div className="icon-tooltip">
        <Icon name="velocidad" alt="Speed" />
        <span className="stat-value">{velocidad}</span>
        <span className="tooltip-text">Speed</span>
      </div>
      <Stars value={Number(velocidad)} />
    </div>
    <div className="stat">
      <div className="icon-tooltip">
        <Icon name="resistencia" alt="Endurance" />
        <span className="stat-value">{resistencia}</span>
        <span className="tooltip-text">Endurance</span>
      </div>
      <Stars value={Number(resistencia)} />
    </div>
    <div className="stat">
      <div className="icon-tooltip">
        <Icon name="agilidad" alt="Agility" />
        <span className="stat-value">{agilidad}</span>
        <span className="tooltip-text">Agility</span>
      </div>
      <Stars value={Number(agilidad)} />
    </div>
  </article>
);

EstadisticasCaballo.propTypes = {
  velocidad: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,
  resistencia: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,
  agilidad: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,
};

export default EstadisticasCaballo;
