import React, { useMemo } from 'react';
import ActuacionesTable from './TablaActuaciones.jsx';
import EquinosTable from './EquinosTableNew.jsx';
import Loader from './../../../../../../generals/loders/Loader.jsx';
import RaceSimulator from "./../../../../races/simuladordecarrera.jsx";

import { useRaceData } from './../../../../../../../hooks/races/useRaceId.jsx';
import { useActuaciones } from './../../../../../../../hooks/races/useActuaciones.jsx';
import { useVariosEquinos } from './../../../../../../../hooks/horses/useVariosEquinos.jsx';
import { useParticipantes } from './../../../../../../../hooks/races/useParticipantes.jsx';
import { useApuestas } from './../../../../../../../hooks/races/useApuestas.jsx';

import { useEquinosNormalizados } from './Actuaciones/useEquinosNormalizado.jsx';
import { useVisualYParticipantesRegistrados } from './Actuaciones/useVisualYParticipantesRegistrados.jsx';

const ActuacionesComponent = ({ raceId }) => {
const raceID = useMemo(() => raceId, [raceId]);
  

  console.log("ELLL PUTO  RACE ID",raceID)
  // Cargar datos de carrera y actuaciones
  const { raceData, error: raceError, isLoading: loadingRace } = useRaceData(raceID);
  const shouldFetchActuaciones = raceData && (raceData.status === 'close' || raceData.status === 'payed');
  const { actuacionesData, error: actuacionesError, isLoading: loadingActuaciones } = useActuaciones(raceID, shouldFetchActuaciones);

  const registrados = raceData?.registrados || [];

  // Memoizar el array registrados para evitar cambios de referencia innecesarios
const registradosMemo = useMemo(() => registrados, [registrados]);

  // Cargar participantes y equinos adicionales usando el memoizado
  const { participantes, loading: loadingParticipantes, error: errorParticipantes } = useParticipantes(registradosMemo, raceID.raceid);
  // const { variosEquinos, loading: loadingVariosEquinos, error: errorVariosEquinos } = useVariosEquinos(registradosMemo);

const { equinos: variosEquinos, loading: loadingVariosEquinos, error: errorVariosEquinos } = useVariosEquinos(registradosMemo);


  // Cargar apuestas
  const { apuestas, loading: loadingApuestas, error: errorApuestas, fetchApuestas } = useApuestas(raceID.raceid);

  // Estado y errores combinados
  const isLoading = loadingRace || loadingActuaciones || loadingParticipantes || loadingVariosEquinos || loadingApuestas;
  console.log("ESTO ESTA CARGADo",isLoading ,loadingRace,loadingActuaciones,loadingParticipantes,loadingVariosEquinos,loadingApuestas)
  const combinedError = raceError || actuacionesError || errorParticipantes || errorVariosEquinos || errorApuestas;

  // Usar hook para unificar equinos normalizados con registrado memoizado
  const equinosNormalizados = useEquinosNormalizados(participantes, variosEquinos, registradosMemo);

  // Usar hook para manejar visual y participantes registrados con registrado memoizado
  const { visual, showParticipantes, participantesRegistrados } = useVisualYParticipantesRegistrados(raceData, equinosNormalizados, registradosMemo);

  if (combinedError) return <p className="error">{combinedError}</p>;
  if (isLoading) return <Loader />;

  return (
    <div className="tabla-actuaaciones">
      {actuacionesData && actuacionesData.length > 0 && participantesRegistrados.length > 0 ? (
        <>
          {visual.length > 0 && (
            <RaceSimulator visual={{ visual }} corredores={{ corredores: participantesRegistrados }} />
          )}
          <ActuacionesTable actuaciones_base={{ actuaciones: actuacionesData, apuestas }} />
        </>
      ) : (
        showParticipantes && equinosNormalizados.length > 0 && (
          <EquinosTable
            equinos={{
              equinosT: equinosNormalizados,
              raceId: raceID.raceid,
              raceName: raceID.name,
              apuestas,
              raceData,
            }}
            fetchApuestas={fetchApuestas} // opcional para refresco manual
          />
        )
      )}
    </div>
  );
};

export default ActuacionesComponent;
