import { useState } from 'react';
import axios from 'axios';
import { linkApp } from './../../components/generals/configuracion/variablesPublicas.jsx';

export function useRegistroCaballo() {
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);
  const [txHash, setTxHash] = useState(null);

  /**
   * Función para registrar un caballo en la carrera, con memo personalizable.
   * 
   * @param {Object} raceData - Datos de la carrera.
   * @param {Object} caballo - Datos del caballo.
   * @param {Object} opciones - Opciones para personalizar el memo.
   *    - memoExtra: String para añadir al memo (opcional).
   *    - memoFunc: Función que recibe { raceData, caballo } y retorna string memo (opcional).
   *    - onSuccess: Función callback que se ejecuta tras registro exitoso (opcional).
   */
  const registrarCaballo = async (raceData, caballo, opciones = {}) => {
    setIsLoading(true);
    setError(null);
    setTxHash(null);

    try {
      // Construcción flexible del memo
      let memo = `EQ- entering a ${caballo.name} Mint ${caballo.identidad} in race ${raceData.raceid}`;

      if (typeof opciones.memoFunc === 'function') {
        memo = opciones.memoFunc({ raceData, caballo });
      } else if (opciones.memoExtra) {
        memo += ` -${opciones.memoExtra}`;
      }

      const toUsername = raceData.CuentaPool;
      const username = localStorage.getItem('user');
      const maxInscripciones = 1; // Equinos permitidos por cuenta
      const amount = raceData.costo_registro;
      const symbol = raceData.symbol_1;

      // Validar estado del caballo
      const equinoResp = await axios.get(`${linkApp}/equino?equineId=${caballo.identidad}`);
      const estado = equinoResp.data.body?.[0]?.estado;
      const status = equinoResp.data.body?.[0]?.status;

      if (!(estado === 'Ready' || !estado) || !(status === 'F')) {
        throw new Error("El caballo ya está registrado o necesita descanso");
      }

      // Verificar inscripciones actuales del usuario
      const raceResp = await axios.get(`${linkApp}/race?raceid=${raceData.raceid}`);
      const registradosOwners = raceResp.data.body?.[0]?.registrados_owners || [];
      const vecesEncontrado = registradosOwners.filter(o => o === username).length;

      if (vecesEncontrado > maxInscripciones) {
        throw new Error(`El usuario ${username} está registrado más de ${maxInscripciones} veces.`);
      }

      // Verificar Hive Keychain está instalado
      if (!window.hive_keychain) {
        throw new Error('Hive Keychain no está instalado');
      }

      const keychain = window.hive_keychain;

      // Realizar la transacción usando Promesa para await
      const response = await new Promise((resolve, reject) => {
        keychain.requestSendToken(
          username,
          toUsername,
          amount.toFixed(3),
          memo,
          symbol,
          (res) => {
            if (res.success) resolve(res);
            else reject(new Error('Transacción fallida o cancelada'));
          }
        );
      });

      // Registrar la transacción en backend
      await axios.post(`${linkApp}/keychaintrx`, response);

      setTxHash(response.transaction_id || null);

      // Callback al éxito si existe
      if (typeof opciones.onSuccess === 'function') {
        opciones.onSuccess(response);
      }

    } catch (err) {
      setError(err.message || 'Error inesperado durante registro');
    } finally {
      setIsLoading(false);
    }
  };

  return { isLoading, error, txHash, registrarCaballo };
}
