// useTorneoData.js
import { useState, useEffect } from 'react';
import { get_Torneo } from './../../components/generals/servicios/torneo/getTorneo.jsx';

const useTorneoData = (isVisibleMail, datosId) => {
  const user = localStorage.getItem('user'); // Obtener usuario actual
  const [torneoData, setTorneoData] = useState({
    premioFase1: null,
    premioFase2: null,
    premioFase3: null,
    premioFase4: null,
    registrados: 0,
    costoRegistro1: null,
    costoRegistro2: null,
    costoRegistro3: null,
    idTorneo: null,
    status: '',
    statusInscripciones: '',
    symbol1Registro: '',
    symbol2Registro: '',
    symbol3Registro: '',
    tipo: '',
    rarezaMax:'',

    registroMax: 0,
    regMaxPorPlayer: 0,
    registrosUsuarioActual: 0
  });
  
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    const fetchTorneoData = async () => {
      if (!isVisibleMail) return;
      
      setLoading(true);
      try {
        const response = await get_Torneo();
        const torneo = response.data.body[0];
        
        // Calcular registros del usuario actual
        const registrosUsuario = Array.isArray(torneo.Registrados) ? 
          torneo.Registrados.filter(registro => registro.habilitador === user).length : 
          0;

        setTorneoData({
          premioFase1: torneo.Premio_Fase_1,
          premioFase2: torneo.Premio_Fase_2,
          premioFase3: torneo.Premio_Fase_3,
          premioFase4: torneo.Premio_Fase_4,
          registrados: torneo.Registrados,
          costoRegistro1: torneo.costo_registro_1,
          costoRegistro2: torneo.costo_registro_2,
          costoRegistro3: torneo.costo_registro_3,
          idTorneo: torneo.id_Torneo,
          status: torneo.status,
          statusInscripciones: torneo.status_inscriciones,
          symbol1Registro: torneo.symbol_1_registro,
          symbol2Registro: torneo.symbol_2_registro,
          symbol3Registro: torneo.symbol_3_registro,
          tipo: torneo.tipo,
          rarezaMax: torneo.rareza_max || "Common" ,
          registroMax: torneo.registro_max || 0,
          regMaxPorPlayer: torneo.reg_max_por_player || 0,
          registrosUsuarioActual: registrosUsuario
        });

      } catch (error) {
        setError(error.response?.data?.message || 'Error al cargar datos del torneo');
      } finally {
        setLoading(false);
      }
    };

    fetchTorneoData();
  }, [isVisibleMail, datosId, user]);

  return {
    ...torneoData,
    loading,
    error,
    actualizarRegistros: (nuevoRegistro) => {
      setTorneoData(prev => ({
        ...prev,
        registrados: prev.registrados + 1,
        registrosUsuarioActual: prev.registrosUsuarioActual + 1
      }));
    }
  };
};

export default useTorneoData;
