import React from 'react';
import { useTranslation } from 'react-i18next';
import { traducirTipo } from './utils'; // Importa la función


const RaceInfo = ({ item, mostrarDivInfoRace, setMostrarDivInfoRace }) => {
  const { t } = useTranslation();

  return (
    <div>
      <p>
        <h4>
          {item.raceid}
          <input
            type="button"
            name="background"
            className="registro"
            value="?"
            style={{ backgroundColor: 'gold' }}
            onClick={(e) => {
              e.stopPropagation();
              setMostrarDivInfoRace(!mostrarDivInfoRace);
            }}
          />
        </h4>
      </p>
      <div>
        <p>{t('tablaCarrera.registrados')}: {item.registrados.length}</p>
        {mostrarDivInfoRace && (
          <div>
            <table>
              <tbody>
                <tr>
                  {Array.isArray(item.registrados_owners) && item.registrados_owners.map((owner, index) => (
                    <td key={index}><span>{owner}</span></td>
                  ))}
                </tr>
              </tbody>
            </table>
            <div>
              <p><h5>{item.name}</h5></p>
              <p>Invited:
                <h5>
                  {item.tipo
                    .split(',')
                    .map((t) => traducirTipo(t))
                    .join(', ')}
                </h5>
              </p>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};


export default RaceInfo;