import React from 'react';
import SponsorHorseCard from './../../../../../../../../games/components/cards/SponsorHorseCard.jsx'; // Ajusta la ruta
import './CaballosTable.css'; // Archivo CSS separado para estilos


const CaballosTable = ({ caballos, onSeleccionar }) => {
  return (
    <div>
       <ul className="caballos-list">
        {caballos.map((horse) => (
          <SponsorHorseCard
            key={horse.equineId}
            horse={horse}
            onSeleccionar={onSeleccionar} // o cambia el nombre si prefieres
          />
        ))}
      </ul>

</div>
  );
};

export default CaballosTable;

