





import React, { useState, useEffect } from 'react';
import './../css/modal/ModalMail.css';

import useTorneoData from './../../../../../../../hooks/torneo/useTorneoData.jsx';
import useWalletData from './../../../../../../../hooks/wallet/useWalletData';
import { registrarCaballo } from './../../../../../../generals/servicios/registrarEnTorneo.jsx';
import RegistroButton from './componentsMMail/botones/RegistroButton';
import { useTranslation } from 'react-i18next';


import {
  calcularCostoFinal,
  getIconBySymbol,
  multiplicadoresRareza,
  getSaldoBySymbol,
  isSaldoInsuficiente,
  isRegistroTorneoLleno,
  isRegistroUsuarioLleno,
} from './Auxiliar/utils';

const ModalMail = ({ isOpenMail, toggleOpenMail, datos, isVisibleMail }) => {
  const [loading, setLoading] = useState(false);
  const [user, setUser] = useState(localStorage.getItem('user'));
  const [showContent, setShowContent] = useState(true);

  const tipoBallo = datos.tipo;
  const animo = datos.animo;

  const [costoFinal1, setCostoFinal1] = useState(0);
  const [costoFinal2, setCostoFinal2] = useState(0);
  const [costoFinal3, setCostoFinal3] = useState(0);
  const [error, setError] = useState({ message: '', type: '' });
   const { t } = useTranslation(); // Inicializa el hook

  let mail = [];
  mail.title = 'Te invitamos a registrar tu equino para la próxima temporada oficial';
  mail.body =
    '<ol style="padding-left: 1px; font-size: 14px; color: #1E3A8A; line-height: 1.25; margin-bottom: 16px; max-width: 280px; word-wrap: break-word;"><p>Cada torneo incluye</p> <p>tres emocionantes fases:</p><li style="margin-bottom: 1px;">Clasificatoria.</li><li style="margin-bottom: 1px;">Semifinales.</li><li style="margin-bottom: 1px;">Gran Final.</li></ol>';
  mail.footer =
    'Este registro asegura la participación de tu equino durante toda la temporada. Ten en cuenta que si no completas el registro a tiempo, se abrirá una inscripción pública y tu equino podría ser auspiciado por otra cuenta, que se llevará los beneficios de la participación. ¡Asegura tu lugar y buena suerte!';

  // Use the hooks
  const {
    saldo,
    saldoBhrt,
    saldoBhr,
    statusRetiro,
    tickets,
    loading: walletLoading,
    error: walletError,
  } = useWalletData(isVisibleMail, user);

  const {
    premioFase1,
    premioFase2,
    premioFase3,
    premioFase4,
    registrados,
    costoRegistro1,
    costoRegistro2,
    costoRegistro3,
    idTorneo,
    status,
    statusInscripciones,
    symbol1Registro,
    symbol2Registro,
    symbol3Registro,
    tipo,
    rarezaMax,
    registroMax,
    regMaxPorPlayer,
    registrosUsuarioActual,
    loading: torneoLoading,
    error: torneoError,
  } = useTorneoData(isVisibleMail, datos.id);

  useEffect(() => {
    if (costoRegistro1 && costoRegistro2 && costoRegistro3) {
      setCostoFinal1(calcularCostoFinal(costoRegistro1, tipoBallo, animo));
      setCostoFinal2(calcularCostoFinal(costoRegistro2, tipoBallo, animo));
      setCostoFinal3(calcularCostoFinal(costoRegistro3, tipoBallo, animo));
    }
  }, [costoRegistro1, costoRegistro2, costoRegistro3, tipoBallo, animo]);

  const closeModal = () => {
    toggleOpenMail();
  };

  const handleRegistro = async (token, costo) => {
    if (isRegistroTorneoLleno(registrados.length, registroMax)) {
      setError({ message: 'Torneo lleno', type: 'error' });
      return;
    }

    if (isRegistroUsuarioLleno(registrosUsuarioActual, regMaxPorPlayer)) {
      setError({ message: 'Límite de registros alcanzado', type: 'error' });
      return;
    }

    setLoading(true);
    setShowContent(false);
    setError({ message: '', type: '' });

    const result = await registrarCaballo({ ...datos, token, costo });

    setLoading(false);
    setError({
      message: result.success ? 'Registro exitoso' : result.error || 'Error desconocido',
      type: result.success ? 'success' : 'error'
    });

    if (!result.success) setShowContent(true);
  };

  const esRarezaPermitida = () => {
    if (!rarezaMax || !datos.tipo) return true;
    return getStringToRareza(datos.tipo) <= getStringToRareza(rarezaMax);
  };

  // const getRarezaString = (rareza) => {
  //   const rarezas = ["Common", "Uncommon", "Rare","Spectral","Epic","Mythic","Legendary"];
  //   return rareza >= 1 && rareza <= rarezas.length ? rarezas[rareza - 1] : "Unknown";
  // };

  const getStringToRareza = (rarezaString) => {
  const rarezas = ["Common", "Uncommon", "Rare", "Spectral", "Epic", "Mythic", "Legendary"];
  const index = rarezas.indexOf(rarezaString);
  return index !== -1 ? index + 1 : null;
};


  return (
    <>
      {isOpenMail && (
        <div className="modalMail-overlay" onClick={closeModal}>
          <div className="modalMail-content" onClick={(e) => e.stopPropagation()}>
            {/* Header */}
            <div className="modalMail-header">
              <div className="sello"></div>
              <button className="close-button-mail" onClick={closeModal}>✖️</button>
            </div>

            {/* Límites de registro */}
            <div className="registro-progress">
              <span>Límite torneo: {registrados.length}/{registroMax} {rarezaMax}</span>
              <span>Tu límite: {registrosUsuarioActual}/{regMaxPorPlayer}</span>
            </div>

            {/* Body */}
            <div className="modalMail-body">
              <div className="letter-container">
                <p className="letter-text">{mail.title}</p>

                {showContent && (
                  <>
                    <div dangerouslySetInnerHTML={{ __html: mail.body }} />
                    <p className="letter-text">{mail.footer}</p>
                  </>
                )}

                {loading ? (
                  <p>Cargando...</p>
                ) : showContent && (
                  <>
                    Torneo: {idTorneo}

                    {/* Mostrar botones solo si hay cupo disponible y la rareza es permitida */}
                    {registrados.length < registroMax && registrosUsuarioActual < regMaxPorPlayer && esRarezaPermitida() ? (
                      <>
                        <RegistroButton
                          symbol={symbol1Registro}
                          costo={costoFinal1}
                          handleRegistro={() => handleRegistro(symbol1Registro, costoFinal1.toFixed(3))}
                          isDisabled={isSaldoInsuficiente(symbol1Registro, costoFinal1, saldoBhrt, saldoBhr, saldo)}
                          getIconBySymbol={getIconBySymbol}
                        />

                        <RegistroButton
                          symbol={symbol2Registro}
                          costo={costoFinal2}
                          handleRegistro={() => handleRegistro(symbol2Registro, costoFinal2.toFixed(3))}
                          isDisabled={isSaldoInsuficiente(symbol2Registro, costoFinal2, saldoBhrt, saldoBhr, saldo)}
                          getIconBySymbol={getIconBySymbol}
                        />

                        <RegistroButton
                          symbol={symbol3Registro}
                          costo={costoFinal3}
                          handleRegistro={() => handleRegistro(symbol3Registro, costoFinal3.toFixed(3))}
                          isDisabled={isSaldoInsuficiente(symbol3Registro, costoFinal3, saldoBhrt, saldoBhr, saldo)}
                          getIconBySymbol={getIconBySymbol}
                        />
                      </>
                    ) : (
                      <div className="alert-rareza">
                        {!esRarezaPermitida() ? (
                          <p>🚫 Rareza Maxima: {rarezaMax}</p>
                        ) : (
                          <p>⚠️ Límite de registros alcanzado.</p>
                        )}
                      </div>
                    )}

                    {/* Mensaje cuando se alcanzan los límites */}
                    {(registrados.length >= registroMax || registrosUsuarioActual >= regMaxPorPlayer) && esRarezaPermitida() && (
                      <div className="alert-limit">
                        {registrados.length >= registroMax && (
                          <p>⚠️ Límite de registros del torneo alcanzado</p>
                        )}
                        {registrosUsuarioActual >= regMaxPorPlayer && (
                          <p>⛔ Has alcanzado tu límite de registros permitidos</p>
                        )}
                      </div>
                    )}
                  </>
                )}

                {/* Mensajes de error */}
                {error.message && (
                  <p className={`error-message-mail ${error.type === 'success' ? 'registroExitosoMail' : ''}`}>
                    {error.message}
                  </p>
                )}
              </div>
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export default ModalMail;
