import { useState, useEffect } from 'react';
import { linkApp } from './../../components/generals/configuracion/variablesPublicas.jsx';

export function useRaceData(raceId) {
  const [raceData, setRaceData] = useState(null);
  const [error, setError] = useState(null);
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    if (!raceId?.raceid) return;

    let isMounted = true;

    const fetchRaceData = async () => {
      setIsLoading(true);
      try {
        const response = await fetch(`${linkApp}/race?raceid=${raceId.raceid}`);

        if (!response.ok) {
          throw new Error(`Error HTTP ${response.status}`);
        }

        const data = await response.json();

        if (!isMounted) return;

        if (data.body && data.body[0]) {
          // Actualiza sólo si es distinto para prevenir renders extras
          setRaceData(prev => (prev !== data.body[0] ? data.body[0] : prev));
          setError(null);
        } else {
          setRaceData(null);
          setError('No se encontró información de la carrera.');
        }
      } catch (e) {
        if (!isMounted) return;
        setRaceData(null);
        setError('Error al cargar los datos de la carrera.');
      } finally {
        if (isMounted) setIsLoading(false);
      }
    };

    fetchRaceData();

    return () => {
      isMounted = false;
    };
  }, [raceId?.raceid]);

  return { raceData, error, isLoading };
}
