// RegistroButton.jsx
import React from 'react';

const RegistroButton = ({ symbol, costo, handleRegistro, isDisabled, getIconBySymbol }) => {
  return (
    <button
      className="action-button"
      onClick={() => handleRegistro(symbol, costo.toFixed(3))}
      disabled={isDisabled}
    >
      <>
        <h2>{isDisabled ? 'Saldo Insuficiente' : 'Registrar'}</h2>
        <p>{symbol} - {costo.toFixed(3)}</p>
        <img
          src={getIconBySymbol(symbol)}
          alt={`${symbol} Token`}
          className="coin"
        />
      </>
    </button>
  );
};

export default RegistroButton;
