import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import './tipoCaballoSelect.css';
import { useTranslation } from 'react-i18next';


const TipoCaballoSelectSimple = ({ tipos, valorInicial, onConfirmar }) => {
   const { t } = useTranslation();

  const [indice, setIndice] = useState(tipos.indexOf(valorInicial) || 0);

  useEffect(() => {
    if (valorInicial) {
      const idx = tipos.indexOf(valorInicial);
      if (idx !== -1) setIndice(idx);
    }
  }, [valorInicial, tipos]);

  const cambiarIndice = (nuevoIndice) => {
    if (nuevoIndice < 0) nuevoIndice = tipos.length - 1;
    else if (nuevoIndice >= tipos.length) nuevoIndice = 0;
    setIndice(nuevoIndice);
  };

  // Ángulo para rotar el cubo (360 grados dividido por número de caras)
  const anguloRotacion = -indice * (360 / tipos.length);

  return (
    <div className="simple-select-container">
      <button
        className="simple-select-arrow"
        onClick={() => cambiarIndice(indice - 1)}
        aria-label="Opción anterior"
      >
        ▲
      </button>

      <div className="dado3d" style={{ transform: `rotateX(${anguloRotacion}deg)` }}>
        {tipos.map((tipo, i) => (
          <div
            key={tipo}
            className="dado3d-cara"
            style={{ transform: `rotateX(${i * (360 / tipos.length)}deg) translateZ(60px)` }}
          >
            {tipo}
          </div>
        ))}
      </div>

      <button
        className="simple-select-arrow"
        onClick={() => cambiarIndice(indice + 1)}
        aria-label="Opción siguiente"
      >
        ▼
      </button>

      <button
        className="simple-select-btn"
        onClick={() => onConfirmar(tipos[indice])}
        disabled={tipos.length === 0}
      >
     {t('racein.siguiente')}
        
      </button>
    </div>
  );
};

TipoCaballoSelectSimple.propTypes = {
  tipos: PropTypes.arrayOf(PropTypes.string).isRequired,
  valorInicial: PropTypes.string,
  onConfirmar: PropTypes.func.isRequired,
};

export default TipoCaballoSelectSimple;

