import { useState, useEffect, useRef } from 'react';
import { linkApp } from './../../components/generals/configuracion/variablesPublicas.jsx';

export function useVariosEquinos(registrados = []) {
  const [equinos, setEquinos] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const isMounted = useRef(true);

  useEffect(() => {
    isMounted.current = true;

    if (!registrados || registrados.length === 0) {
      if (isMounted.current) setEquinos([]);
      return;
    }

    const fetchEquinos = async () => {
      if (!isMounted.current) return;

      setLoading(true);
      setError(null);

      try {
        const numerosConsulta = registrados.join(',');
        const response = await fetch(`${linkApp}/equino?equineId=${numerosConsulta}`);

        if (!response.ok) {
          throw new Error(`Error HTTP ${response.status}`);
        }

        const data = await response.json();

        console.log('Respuesta cruda en useVariosEquinos:', data);

        if (data.error) {
          throw new Error(data.error);
        }

        const equinosNuevos = data.body || [];

        if (!isMounted.current) return;

        setEquinos((prevEquinos) => {
          const combinados = [
            ...prevEquinos,
            ...equinosNuevos.filter(
              (nuevo) => !prevEquinos.some((existente) => existente.equineId === nuevo.equineId)
            ),
          ];

          const ordenados = registrados.map((id) => {
            const encontrado = combinados.find(
              (equino) => String(equino.equineId) === String(id)
            );
            if (!encontrado) {
              console.warn(`ID ${id} en registrados no tiene coincidencia en los datos obtenidos.`);
            }
            return encontrado || { equineId: id, missing: true };
          });

          const filtrados = ordenados.filter((e) => !e.missing);

          // Evitar update si los datos no cambiaron
          try {
            const iguales = JSON.stringify(prevEquinos) === JSON.stringify(filtrados);
            return iguales ? prevEquinos : filtrados;
          } catch {
            return filtrados;
          }
        });
      } catch (err) {
        if (!isMounted.current) return;
        setError(err.message || 'Error al cargar datos de equinos');
      } finally {
        if (isMounted.current) setLoading(false);
      }
    };

    fetchEquinos();

    return () => {
      isMounted.current = false;
    };
  }, [registrados]);

  return { equinos, loading, error };
}
