// PrizeToggle.jsx
import React from 'react';
import { useTranslation } from 'react-i18next';

const PrizeToggle = ({ visiblePrize, toggleVisibility }) => {
  const { t } = useTranslation();

  return (
    <button onClick={toggleVisibility}>
      {visiblePrize ? t('tablaCarrera.ocultarPremio') : t('tablaCarrera.mostrarPremio')}
    </button>
  );
};

export default PrizeToggle;
