import React from 'react';
import PropTypes from 'prop-types';
import './../css/EstadisticaCaballo.css'; // Asegúrate de tener los estilos

export function Stars({ value }) {
  const max = 200;
  const percentage = Math.min(value / max, 1) * 100;
  const stars = [0, 1, 2].map((i) => {
    const starFill = Math.min(Math.max(percentage - i * 33.33, 0), 33.33);
    const fillPercent = (starFill / 33.33) * 100;
    return fillPercent;
  });

  return (
    <div className="stars-container">
      {stars.map((fill, i) => (
        <div key={i} className="star">
          <div className="star-fill" style={{ width: `${fill}%` }} />
          <svg
            className="star-cube"
            viewBox="0 0 24 24"
            fill="none"
            stroke="#0ff"
            strokeWidth="2"
            strokeLinecap="round"
            strokeLinejoin="round"
          >
            <polygon points="12 2 15 8 22 9 17 14 18 21 12 18 6 21 7 14 2 9 9 8 12 2" />
          </svg>
        </div>
      ))}
    </div>
  );
}

Stars.propTypes = {
  value: PropTypes.number.isRequired,
};
