import React from 'react';
import './css/Prizes.css';
import logos from '../../../../../../../../../../assets/logoblockhorseracing/coinBHRT2.png';
import logosBHR from '../../../../../../../../../../assets/logoblockhorseracing/logoBlock.png';
import logoHive from '../../../../../../../../../../assets/logoblockhorseracing/logoHive.png';

const CoinPrize = ({ logoSrc, prize, name }) => (
  <div className="coin-item" title={name}>
    {/* También podrías poner el title en la imagen si prefieres */}
    <div className="coin-base neon-glow">
      <div className="coin-window neon-window">
        <img src={logoSrc} alt={name} title={name} className="coin-logo neon-logo" />
      </div>
    </div>
    <div className="prize-value">{prize}</div>
  </div>
);

const CoinPrizeContainer = ({ raceData }) => {
  // Función para determinar el logo según el símbolo
  const getLogoBySymbol = (symbol) => {
    switch (symbol?.toUpperCase()) {
      case 'BHRT':
        return logos;
      case 'BHR':
        return logosBHR;
      case 'HIVE':
        return logoHive;
      default:
        return logos; // Logo por defecto
    }
  };

  // Calcula los premios según raceData, verifica que no sean cero o falsy
  const premios = [
    raceData ? (raceData.Pote + (raceData.costo_registro * 8)) : 0,
    raceData ? raceData.premiostotal_2 : 0,
    raceData ? raceData.premiostotal_3 : 0,
  ];

  // Arma array de monedas filtrando las que tienen premio > 0, incluye el nombre para tooltip
  const coins = [
    { logoSrc: getLogoBySymbol(raceData?.symbol_1), prize: premios[0], name: raceData?.symbol_1 },
    { logoSrc: getLogoBySymbol(raceData?.symbol_2), prize: premios[1], name: raceData?.symbol_2 },
    { logoSrc: getLogoBySymbol(raceData?.symbol_3), prize: premios[2], name: raceData?.symbol_3 },
  ].filter(coin => coin.prize > 0);

  return (
    <section className="coin-prize-container-wrapper">
      <h3 className="prizes-title">Prizes to be Distributed</h3>
      <section className="coin-prize-container">
        {coins.length > 0 ? (
          coins.map((coin, idx) => (
            <CoinPrize key={idx} logoSrc={coin.logoSrc} prize={coin.prize} name={coin.name} />
          ))
        ) : (
          <p>No prizes to display.</p>
        )}
      </section>
    </section>
  );
};

export default CoinPrizeContainer;

