import React from 'react';
import PremiosList from './PremiosList.jsx';
import { useTranslation } from 'react-i18next';

export default function ParticipacionItem({ d }) {
  const { t } = useTranslation();

  // Función para formatear número según primeros 4 decimales
  function formatearNumeroPremio(valor) {
    const num = Number(valor);
    if (isNaN(num)) return valor; // Si no es número, devolver tal cual
    const decimales = num.toString().split('.')[1] || '';
    const primeros4 = decimales.slice(0, 4);
    if (primeros4 === '' || /^0{1,4}$/.test(primeros4)) {
      return num.toFixed(0); // Mostrar entero
    }
    return num.toFixed(4).replace(/\.?0+$/, ''); // Hasta 4 decimales sin ceros finales
  }

  return (
    <li className="history-sponsor-item">
      <div><strong>{t('race')}:</strong> {d.race}</div>
      <div>
        <strong>{t('equine')}:</strong> {d.equinoName} {d.equineId && `(${d.equineId})`}
      </div>
      <div>
        <strong>{t('entryCost')}:</strong>
        <ul className="history-sponsor-sub-list">
          <li>
            {t('quantity')}: {formatearNumeroPremio(d.r_quantity)} {d.r_symbol || 'N/A'}
          </li>
        </ul>
      </div>
      <div><strong>{t('arrivalPosition')}:</strong> {d.puesto_Llegada}</div>
      <div>
        <strong>{t('officialPrizes')}:</strong>
        <PremiosList premios={d.premios.oficial} estados={d.premios.estados} />
      </div>
    </li>
  );
}
