import { useNavigate } from 'react-router-dom';
import "./BurnVoteButton.css";
import "./BurnVoteButtonFlame.css";

export default function BurnVoteButton({ className = '' }) {
  const navigate = useNavigate();

  const handleClick = () => {
    navigate("/burntovote");
  };



return (
<button className={`burn-vote-button ${className}`} onClick={handleClick}>
  <span className="burn-fire-icon">🔥</span>

  <span className="burn-sign-text">BURN TO VOTE</span>

  {/* Contenedor humo constante */}
  <span className="burn-smoke-container">
    {[...Array(10)].map((_, i) => (
      <span key={i} className="burn-smoke-particle"></span>
    ))}
  </span>

  <span className="burn-floating-text">
    <span className="burn-word burn-burn">BURN</span>
    <span className="burn-word burn-to">TO</span>
    <span className="burn-word burn-vote">VOTE</span>
  </span>
</button>
);





}



