import React, { useState } from 'react';
import { useCaballosPorRareza } from './../../../../../../../../hooks/horses/horsesByRarity.jsx'; // Ajusta ruta
import CaballosTable from './components/CaballosTable.jsx'; // Componente tabla separado
import ConfirmarAuspicio from './components/ConfirmarAuspicio.jsx'; // Componente tabla separado

import TipoCaballoSelect from './components/tipoCaballoSelect.jsx'; // Ajusta la ruta
import { useRegistroCaballo } from './../../../../../../../../hooks/races/useRegistrarCaballo.jsx'; // Ajusta ruta
import './css/AuspiciarModulo.css';
import { useTranslation } from 'react-i18next';


const tiposCaballo = ['Legendary', 'Mythic', 'Epic', 'Spectral', 'Rare', 'Uncommon', 'Common'];

const AuspiciarModulo = ({ raceData, onCerrar }) => {

  const [tipoSeleccionado, setTipoSeleccionado] = useState(null);
  const [caballoSeleccionado, setCaballoSeleccionado] = useState(null);

  const {
    isLoading,
    error,
    txHash,
    registrarCaballo,
  } = useRegistroCaballo();

  const { caballos: caballosPermitidos, loading: loadingCaballos, error: errorCaballos } = useCaballosPorRareza(tipoSeleccionado);
   const { t } = useTranslation();


// const handleConfirmar = async () => {
//   if (!caballoSeleccionado) {
//     alert('Seleccione un caballo antes de confirmar');
//     return;
//   }
//   console.log('ID del caballo seleccionado:', caballoSeleccionado.equineId); // <-- Aquí
//   console.log('ID del caballo seleccionado:', caballoSeleccionado); // <-- Aquí

//   await registrarCaballo(raceData, caballoSeleccionado, {
//     memoExtra: 'Registro desde AuspiciarModulo',
//     onSuccess: () => {
//       console.log('Registro exitoso');
//     },
//   });
// };



const handleConfirmar = async () => {
  if (!caballoSeleccionado) {
    alert('Seleccione un caballo antes de confirmar');
    return;
  }

  // Normalizar sólo los datos que necesita el hook
  const caballoParaRegistro = {
    identidad: caballoSeleccionado.equineId,
    name:  caballoSeleccionado.nameBhr ?? caballoSeleccionado.name ??'',
  };

  console.log('Objeto normalizado a registrar:', caballoParaRegistro);

  await registrarCaballo(raceData, caballoParaRegistro, {
    memoExtra: 'S',
    onSuccess: () => {
      console.log('Registro exitoso');
    },
  });
};

  return (
    <div className="auspiciar-modulo-overlay">
      <div className="auspiciar-modulo-container container">
        <button onClick={onCerrar} className="close-button-auspiciar">X</button>
        <h2>Race {raceData.raceid}</h2>

        {!tipoSeleccionado && (
          <>
            <h3>{t('auspiciar.SelectRareza')}</h3>
            <TipoCaballoSelect
              tipos={tiposCaballo}
              valorInicial={tipoSeleccionado}
              onConfirmar={(seleccion) => {
                setTipoSeleccionado(seleccion);
                setCaballoSeleccionado(null);
              }}
            />
          </>
        )}

        {tipoSeleccionado && !caballoSeleccionado && (
          <>
            <h3>Caballos permitidos para tipo <em>{tipoSeleccionado}</em></h3>
            {loadingCaballos && <p>Cargando caballos...</p>}
            {errorCaballos && <p className="error-message">Error al cargar caballos: {errorCaballos.message}</p>}
            {!loadingCaballos && !errorCaballos && caballosPermitidos.length === 0 && (
              <p>No hay caballos disponibles para este tipo.</p>
            )}
            {!loadingCaballos && !errorCaballos && caballosPermitidos.length > 0 && (
              <CaballosTable
  caballos={caballosPermitidos}
  onSeleccionar={(caballo) => {
    console.log('Caballo seleccionado:', caballo); // <-- Aquí, cuando seleccionas uno
    setCaballoSeleccionado(caballo);
  }}
/>



            )}
            <button className="back-button-auspicio" onClick={() => setTipoSeleccionado(null)}>Volver</button>
          </>
        )}

        {caballoSeleccionado && !txHash && (
          <ConfirmarAuspicio
            tipoSeleccionado={tipoSeleccionado}
            caballoSeleccionadoId={caballoSeleccionado.equineId}
            firmando={isLoading}
            onConfirmar={handleConfirmar}
            onVolver={() => setCaballoSeleccionado(null)}
            raceData={raceData}
            error={error}
          />
        )}

        {txHash && (
          <>
            <h3>¡Auspicio registrado con éxito!</h3>
            <p>Hash de transacción: {txHash}</p>
            <button onClick={onCerrar}>Cerrar</button>
          </>
        )}
      </div>
    </div>
  );
};

export default AuspiciarModulo;
