import { useState, useEffect, useRef } from 'react';
import { linkApp } from './../../components/generals/configuracion/variablesPublicas.jsx';

export function useParticipantes(registrados = [], numeroCarrera) {
  const [participantes, setParticipantes] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const isMounted = useRef(true);

  useEffect(() => {
    isMounted.current = true;

    if (!registrados || registrados.length === 0 || !numeroCarrera) {
      if (isMounted.current) setParticipantes([]);
      return;
    }

    const fetchParticipantes = async () => {
      if (!isMounted.current) return;

      setLoading(true);
      setError(null);

      try {
        const numerosConsulta = registrados.join(',');
        const url = `${linkApp}/participantes?equineId=${numerosConsulta}&race=${numeroCarrera}`;
        console.log('Fetch URL en useParticipantes:', url);

        const response = await fetch(url);

        const text = await response.text();
        console.log('Respuesta cruda en useParticipantes:', text);

        let data;
        try {
          data = JSON.parse(text);
        } catch (parseError) {
          throw new Error('La respuesta no es JSON válido');
        }

        if (data.error) {
          throw new Error(data.error);
        }

        const participantesNuevos = data.body || [];

        if (!isMounted.current) return;

        setParticipantes((prevParticipantes) => {
          const combinados = [
            ...prevParticipantes,
            ...participantesNuevos.filter(
              (nuevo) =>
                !prevParticipantes.some(
                  (existente) => existente.equineId === nuevo.equineId
                )
            ),
          ];

          const ordenados = registrados.map((id) => {
            const encontrado = combinados.find(
              (p) => String(p.equineId) === String(id)
            );
            if (!encontrado) {
              console.warn(
                `ID ${id} en registrados no tiene coincidencia en los datos obtenidos.`
              );
            }
            return encontrado || { equineId: id, missing: true };
          });

          const filtrados = ordenados.filter((e) => !e.missing);

          // Evitar setState si no hay cambios reales
          try {
            const iguales = JSON.stringify(prevParticipantes) === JSON.stringify(filtrados);
            return iguales ? prevParticipantes : filtrados;
          } catch {
            return filtrados;
          }
        });
      } catch (err) {
        if (!isMounted.current) return;
        setError(err.message || 'Error al cargar datos de participantes');
      } finally {
        if (isMounted.current) setLoading(false);
      }
    };

    fetchParticipantes();

    return () => {
      isMounted.current = false;
    };
  }, [registrados, numeroCarrera]);

  return { participantes, loading, error };
}
