import { useState, useEffect } from 'react';
import { linkApp } from './../../components/generals/configuracion/variablesPublicas.jsx';

export function useRaces(filterRacetype, filterByDate) {
  const [allRaces, setAllRaces] = useState([]);
  const [races, setRaces] = useState([]);
  const [recentRaces, setRecentRaces] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  // Función para obtener las 6 últimas carreras ordenadas
  const getRecentRaces = (allRaces) => {
    const lastSixRaces = allRaces.slice(0, 6);
    const sortedRecentRaces = lastSixRaces.sort((a, b) => a.raceid - b.raceid);
    return sortedRecentRaces;
  };

  // Función para ordenar las carreras según filtro
  const applySorting = (allRaces) => {
    let sortedRaces;

    if (filterByDate) {
      // Ordenar por fecha de salida descendente
      sortedRaces = allRaces.sort(
        (a, b) => new Date(b.tiempodesalida) - new Date(a.tiempodesalida)
      );
    } else {
      // Ordenar por raceid descendente
      sortedRaces = allRaces.sort((a, b) => b.raceid - a.raceid);
    }

    setRaces(sortedRaces);
    setRecentRaces(getRecentRaces(sortedRaces));
    setLoading(false);
  };

const fetchRaces = async () => {
  setLoading(true);
  setError(null);

  try {
    const raceType = filterRacetype ? 'Implemento ' : 'Oficial';
    const url = `${linkApp}/race?page=1&type=${raceType}`;

    const response = await fetch(url);
    const data = await response.json();

    if (data.error) {
      setError(data.error);
      setAllRaces([]);
      setRaces([]);
      setRecentRaces([]);
      setLoading(false);
      return;
    }

    setAllRaces(data.body || []);
    setLoading(false);
  } catch (error) {
    setError('Error al obtener las carreras.');
    setAllRaces([]);
    setRaces([]);
    setRecentRaces([]);
    setLoading(false);
  }
};




useEffect(() => {
  // Fetch solo cuando filterRacetype cambia
  fetchRaces();
}, [filterRacetype]);

// Nuevo useEffect para ordenar carreras localmente
useEffect(() => {
  if (allRaces.length > 0) {
    let sortedRaces;
    if (filterByDate) {
      sortedRaces = [...allRaces].sort(
        (a, b) => new Date(b.tiempodesalida) - new Date(a.tiempodesalida)
      );
    } else {
      sortedRaces = [...allRaces].sort((a, b) => b.raceid - a.raceid);
    }
    setRaces(sortedRaces);
    setRecentRaces(getRecentRaces(sortedRaces));
  }
}, [allRaces, filterByDate]);




  return { races, recentRaces, loading, error, fetchRaces };
}
