import React from 'react';
import EquineImage from './../EquineImage';
import LastRacesEq from './LastRacesEq';
import Apuestas from './../Apuestas';
import EquinoStats from './EquinosStats';
import EquineBasicInfo from './EquineBasicInfo.jsx';
import './../css/EquinosTabla/EquineinfoOwner.css'
import hivelogo from './../../../../../../../../assets/logoblockhorseracing/logoHive.png';

import { useTranslation } from 'react-i18next';


const EquinoRow = ({ equino, index, raceId, apuestas, fetchApuestas }) => {
   const { t } = useTranslation();
  
  const sumaTicketsPorEquino = apuestas.apuestas.reduce((acc, apuesta) => {
    if (apuesta.equinoId === equino.equineId) return acc + apuesta.cantidadTickets;
    return acc;
  }, 0);

const isMobile = window.innerWidth < 600;

return (
 isMobile ? (
 <div className="equino-card">
  {/* Parte superior: imagen/info cuenta + stats + carreras */}
  <div className="equino-card-top">
    {/* Columna 1: datos básicos e imagen */}
    <div className="equino-card-col equino-info">
      <h3 className="equino-index">{index}</h3>
      <EquineBasicInfo equino={equino} />
      {equino.habilitado === "si" && equino.status_oficial === "R" && (
        <div className="equino-habilitador">📜 {equino.habilitador}</div>
      )}

      <div className="equino-support">
        {t("apuestas.ESupport")} {(sumaTicketsPorEquino / 20).toFixed(4)}<img src={hivelogo} alt="Token" className="coin" /> 
      </div>
      <div className="equino-account">👑{equino.account}</div>
{equino.habilitador !== equino.account && (
  <div className="equino-account">📜{equino.habilitador}</div>
)}

    </div>

 {/* Columna 3: últimas carreras */}
    <div className="equino-card-col equino-races">
      <LastRacesEq caballo={equino} />
    </div>

    {/* Columna 2: stats */}
    <div className="equino-card-col equino-stats">
      <EquinoStats equino={equino} />
    </div>

   
  </div>

  {/* Parte inferior: apuestas */}
  <div className="equino-card-footer">
    <Apuestas
      equino={{
        equineId: equino.equineId,
        name: equino.name,
        raceId,
        apuestas
      }}
      fetchApuestas={fetchApuestas}
    />
  </div>
</div>
) : (
  /* Versión escritorio normal */
  <tr className="equino-master-row-fila1">
    <td>
      <h1 className="equino-index">{index}</h1>
     
      <div className="equino-support">
        {t('apuestas.ESupport')} {(sumaTicketsPorEquino / 20).toFixed(4)} <img src={hivelogo} alt="Token" className="coin" />
      </div>
      <div className="equino-account">👑{equino.account}</div>
{equino.habilitador !== equino.account && (
  <div className="equino-account">📜{equino.habilitador}</div>
)}
       {equino.habilitado === "si" && equino.status_oficial === "R" && (
        <div className="equino-habilitador">📜 {equino.habilitador}</div>
      )}

    </td>
    <td><EquineBasicInfo equino={equino} /></td>
    <td><EquinoStats equino={equino} /></td>
    <td><LastRacesEq caballo={equino} /></td>
    <td>
      <Apuestas
        equino={{
          equineId: equino.equineId,
          name: equino.name,
          raceId,
          apuestas
        }}
        fetchApuestas={fetchApuestas}
      />
    </td>
  </tr>
)
 
);

};

export default EquinoRow;
