import React, { useState } from 'react';
import Alert from './AlertTabla.jsx';
import { linkApp } from './../../../generals/configuracion/variablesPublicas.jsx';

import TimeAPI from './../auxiliar/reloj.jsx';
import axios from 'axios';
import { useTranslation } from 'react-i18next';

import Premio from './componentes_tables/prize_race.jsx';
import RaceInfo from './componentes_tables/TablaCarreras/RaceInfo.jsx';
import PrizeToggle from './componentes_tables/TablaCarreras/PrizeToggle.jsx';
import EntryButton from './componentes_tables/TablaCarreras/EntryButton.jsx';
import { minutosFaltantes } from './componentes_tables/TablaCarreras/utils.jsx';

import { useRegistroCaballo } from './../../../../hooks/races/useRegistrarCaballo.jsx'; // Ajusta la ruta real

import './tablaDatos.css';

export default function TablaCarreras(props) {
  const [mostrarDiv, setMostrarDiv] = useState(false);
  const [mostrarDivInfoRace, setMostrarDivInfoRace] = useState(false);
  const [mostrarBoton_Inscripcion, setMostrarBoton_Inscripcion] = useState(true);
  const { timeActual, setTimeActual } = TimeAPI();
  const { data, propsi, handlee, fecha } = props;

  const [visiblePrize, setVisiblePrize] = useState(false);

  const toggleVisibility = () => {
    setVisiblePrize(!visiblePrize);
  };

  const user = localStorage.getItem('user');

  const reversedData = [...data].reverse();



  const { isLoading, error, txHash, registrarCaballo } = useRegistroCaballo();

  const { t } = useTranslation();

  function calcularPremio(item) {
    const registradosLength = item.registrados.length || 0;

    if (registradosLength < 8) {
      return item.costo_registro * 8 + item.Pote;
    } else {
      return item.premiostotal_1;
    }
  }

  const handleFetch = async (raceData, caballo) => {
    await registrarCaballo(raceData, caballo, {
      
      onSuccess: () => {
        setMostrarBoton_Inscripcion(false);
        handlee(propsi);
      },
    });
  };

  return (
    <div className="tableInscripcion" id="tablaInscripcion">
      {reversedData.map((item) => (

        <div key={item.raceid} className="rowInscripcion">
          {/* Información */}
          <div className="cell">
            <RaceInfo
              item={item}
              mostrarDivInfoRace={mostrarDivInfoRace}
              setMostrarDivInfoRace={setMostrarDivInfoRace}
            />
          </div>

          {/* Premios */}
          <PrizeToggle visiblePrize={visiblePrize} toggleVisibility={toggleVisibility} />

          {visiblePrize && (
            <div className="cell">{item && <Premio item={item} />}</div>
          )}

          {/* Salida */}
          <div className="cell">
            <p>
              {t('tablaCarrera.salida')} :
              {minutosFaltantes(item.tiempodesalida, timeActual) >= 60
                ? `${Math.floor(minutosFaltantes(item.tiempodesalida, timeActual) / 60)} hrs`
                : `${minutosFaltantes(item.tiempodesalida, timeActual)} min`}
        console.log('tiempodesalida:XXXXX', item.tiempodesalida)
        console.log('Item completo:', item);


            </p>
            <p>
              fee: {item.costo_registro}-{item.symbol_1}
            </p>

            <EntryButton
              item={item}
              user={user}
              timeActual={timeActual}
              handleFetch={handleFetch}
              propsi={propsi}
              mostrarBoton_Inscripcion={mostrarBoton_Inscripcion}
              isLoading={isLoading}
              errorTransaccion={error}
              txHash={txHash}
            />
          </div>
        </div>
      ))}
      {error && <Alert message={error} />}
    </div>
  );
}
