// utils.js o utils.jsx
export function minutosFaltantes(tiempoDesalida, timeActual) {
  const tiempoActual = new Date(timeActual);
  const tiempoDesalidaDate = new Date(tiempoDesalida);
  const diferencia = tiempoDesalidaDate - tiempoActual;
  const minutosFaltantes = Math.ceil(diferencia / (1000 * 60));

  if (minutosFaltantes <= 0) {
    return 0;
  } else {
    return minutosFaltantes;
  }
}

const traducciones = {
  'D0': 'Debutante',
  'G1': 'Ganadores de 1',
  'G2': 'Ganadores de 2',
  'G3': 'Ganadores de 3',
  'G4': 'Ganadores de 4',
  'G5': 'Ganadores de 5',
  'L0':'Libre',
  'P0': 'Perdedores',
  'C1': 'Ganadores de 1*',
  'C2': 'Ganadores de 2*',
  'C3': 'Ganadores de 3*',
  'C4': 'Ganadores de 4*',
  'C5': 'Ganadores de 5* o mas',
  'P3': 'Perdedores de 3*',
  'P4': 'Perdedores de 4*',
  'P5': 'Perdedores de 5 o mas',
};

export function traducirTipo(tipo) {
  return traducciones[tipo] || '';
}
