import { useMemo } from 'react';
import { normalizarEquino, normalizarParticipante, esEquinoValido } from './normalizeEquino.jsx';

export const useEquinosNormalizados = (participantes, variosEquinos, registrados) => {

  console.log("useEquinosNormalizados desde ya ya ya aya ", registrados,"PARTICIPANTES",participantes,"VARIOSEQUINOS",variosEquinos)
  
  return useMemo(() => {
    const participantesNorm = (participantes ?? []).map(normalizarParticipante);

console.log('variosEquinos antes de normalizar:', variosEquinos);
    console.log('Ejecutando normalización variosEquinos, count:', (variosEquinos ?? []).length);
const variosNorm = (variosEquinos ?? []).map(normalizarEquino);

    // const variosNorm = (variosEquinos ?? []).map(normalizarEquino);

    return (registrados ?? []).map((id) => {
      const idStr = String(id);

      // Buscar en participantes
      const deParticipantes = participantesNorm.find(eq => String(eq.equineId) === idStr);

      const esValidoParticipantes = esEquinoValido(deParticipantes);

      console.log(`ID ${id} -> Participantes:`, deParticipantes);
      console.log(`ID ${id} -> ¿Participante válido?`, esValidoParticipantes);

      if (esValidoParticipantes) {
        console.log(`ID ${id} -> Usando dato de PARTICIPANTES`);
        return deParticipantes;
      }

      // Buscar en variosEquinos
      const deVarios = variosNorm.find(eq => String(eq.equineId) === idStr);
      const esValidoVarios = esEquinoValido(deVarios);

      console.log(`ID ${id} -> VariosEquinos:`, deVarios);
      console.log(`ID ${id} -> ¿VariosEquinos válido?`, esValidoVarios);

      if (esValidoVarios) {
        console.log(`ID ${id} -> Usando dato de VARIOSEQUINOS`);
        return deVarios;
      }

      console.log(`ID ${id} -> NO hay dato válido en PARTICIPANTES ni VARIOSEQUINOS, usando objeto por defecto`);
      return {
        equineId: id,
        name: 'Sin datos',
        agilidad: 0,
        animo: 0,
        velocidad: 0,
        resistencia: 0,
        estado: 'Desconocido',
        tipo: 'Desconocido',
        salud: '0',
        sexo: 'N/A',
      };
    });
  }, [participantes, variosEquinos, registrados]);
};
