import React, { useState } from 'react';
import ModalAuspicio from './ModalAuspicio/ModalAuspicio.jsx';
import CardResumen from './CardHistoryAuspicio/CardResumen.jsx';

export default function AuspicieHistoryContainer() {
  const [modalOpen, setModalOpen] = useState(false);
  const usuario = localStorage.getItem('user') || '';

  const openModal = () => setModalOpen(true);
  const closeModal = () => setModalOpen(false);

  return (
    <div>
      <CardResumen usuario={usuario} onOpenModal={openModal} />
      <ModalAuspicio usuario={usuario} onClose={closeModal} isOpen={modalOpen} />
    </div>
  );
}
