// utils.js
import hivelogo from '../../../../../../../../assets/logoblockhorseracing/logoHive.png';
import logosBHR from '../../../../../../../../assets/logoblockhorseracing/logoBlock.png';
import logosBHRT from '../../../../../../../../assets/logoblockhorseracing/coinBHRT2.png';

export const multiplicadoresRareza = {
  Common: 1,
  Uncommon: 2,
  Rare: 3,
  Spectral: 5,
  Epic: 8,
  Mythic: 10,
  Legendary: 12.5,
  XCommon: 1,
  XUncommon: 2,
  XRare: 3,
  XSpectral: 5,
  XEpic: 8,
  XMythic: 10,
  XLegendary: 12.5,
};

// Función para calcular el costo basado en la rareza y la animo del caballo
export const calcularCostoFinal = (costoBase, rareza, animo) => {
  let multiplicador = multiplicadoresRareza[rareza] || 1; // Aplicar el multiplicador de rareza

  // Aplicar el multiplicador adicional basado en la animo del caballo
  if (animo <= 100 && animo > 50) {
    multiplicador *= 1; // Si la animo está entre 100 y 51, el costo no cambia (x1)
  } else if (animo <= 50 && animo > 20) {
    multiplicador *= 2; // Si la animo está entre 50 y 21, el costo se duplica (x2)
  } else if (animo <= 20 && animo > 0) {
    multiplicador *= 3; // Si la animo está entre 20 y 1, el costo se triplica (x3)
  } else if (animo <= 0) {
    multiplicador *= 4; // Si la animo es 0 o negativa, el costo se cuadruplica (x4)
  }

  return costoBase * multiplicador; // Devolver el costo final con el multiplicador aplicado
};

export const getIconBySymbol = (symbol) => {
  switch (symbol) {
    case 'BHRT':
      return logosBHRT;
    case 'SWAP.HIVE':
      return hivelogo;
    case 'BHR':
      return logosBHR;
    default:
      return hivelogo; // O un ícono por defecto si lo necesitas
  }
};

// Function to get balance based on symbol
export const getSaldoBySymbol = (symbol, saldoBhrt, saldoBhr, saldo) => {
  switch (symbol) {
    case 'BHRT':
      return saldoBhrt;
    case 'HIVE':
      return saldoBhr;
    default:
      return saldo; // General balance
  }
};

// Function to check if balance is insufficient
export const isSaldoInsuficiente = (symbol, costo, saldoBhrt, saldoBhr, saldo) => {
  const saldoActual = getSaldoBySymbol(symbol, saldoBhrt, saldoBhr, saldo);
  return saldoActual < costo;
};




// utils.js
export const isRegistroTorneoLleno = (registrados, registro_max) => {
  return registrados >= registro_max;
};

export const isRegistroUsuarioLleno = (registrosUsuario, reg_max_por_player) => {
  return registrosUsuario >= reg_max_por_player;
};
