

import React from 'react';
import PropTypes from 'prop-types';
import { useEquino } from './../../../../../../../../../hooks/horses/horseById.jsx';

import DatosCaballo from './ConfirmarAuspicio/DatosCaballo';
import EstadisticasCaballo from './ConfirmarAuspicio/EstadisticasCaballo';
import UltimasCarreras from './ConfirmarAuspicio/UltimasCarreras';
import AccionesBotones from './ConfirmarAuspicio/AccionesBotones';
import Prizes from './ConfirmarAuspicio/Prizes'; // Ajusta la ruta según tu estructura

import './ConfirmarAuspicio.css';

const ConfirmarAuspicio = ({
  tipoSeleccionado,
  caballoSeleccionadoId,
  firmando,
  onConfirmar,
  onVolver,
  raceData,
}) => {
  const { equino, loading, error } = useEquino(caballoSeleccionadoId);

  if (loading) return <p className="loading">Cargando información del caballo...</p>;
  if (error) return <p className="error">Error al cargar el caballo: {error.message}</p>;
  if (!equino) return <p className="error">No se encontró información del caballo.</p>;


  const velocidadTotal = (Number(equino.velocidad) + Number(equino.velocidad_add || 0)).toFixed(2);
  const resistenciaTotal = (Number(equino.resistencia) + Number(equino.resistencia_add || 0)).toFixed(2);
  const agilidad = equino.agilidad ?? 'N/A';


  return (
  <section className="confirmar-auspicio-container">

    <DatosCaballo equino={equino} />

    <EstadisticasCaballo
      velocidad={velocidadTotal}
      resistencia={resistenciaTotal}
      agilidad={agilidad}
    />

    <UltimasCarreras carreras={equino.carreras} />
     <AccionesBotones
      firmando={firmando}
      onConfirmar={onConfirmar}
      onVolver={onVolver}
      raceData={raceData}
    />

    <Prizes 
    raceData={raceData} 
    />  {/* Aquí insertamos el componente de premios */}

   
  </section>
);

};

ConfirmarAuspicio.propTypes = {
  tipoSeleccionado: PropTypes.string.isRequired,
  caballoSeleccionadoId: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,
  firmando: PropTypes.bool.isRequired,
  onConfirmar: PropTypes.func.isRequired,
  onVolver: PropTypes.func.isRequired,
};

export default ConfirmarAuspicio;
